@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
.feed{width:100%;display:flex;flex-direction:column;padding:16px}@media screen and (min-width: 728px){.feed{padding:16px 0}}.new-user .user__thumb{width:120px;height:120px;margin:20px auto}.new-user .user__name{font-size:2.4rem;font-weight:bold;margin:0 auto;text-align:center}.new-user .post__form{padding:40px;background-color:0,0,0,0.5;border-top:1px solid rgba(0,0,0,0.1);display:flex;flex-direction:column;justify-content:center}.new-user .post__form label{display:block;font-size:1.2rem;margin-bottom:5px;font-weight:bold}.new-user .post__form input{font-size:16px;padding:8px;margin-bottom:20px}.new-user .post__form button{background-color:black;color:white;font-size:16px;font-weight:bold;padding:8px 20px}

.fas.fa-spinner{color:rgba(0,0,0,0.2);font-size:50px;left:50%;position:absolute;top:50%;transform:translate(-50%);-webkit-animation:gira-gira infinite 1s;animation:gira-gira infinite 1s}@-webkit-keyframes gira-gira{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes gira-gira{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.post{background-color:white;border-radius:10px;border:1px solid rgba(0,0,0,0.1);display:flex;flex-direction:column;overflow:hidden;margin-bottom:30px}.post__header{align-items:center;display:flex;justify-content:space-between;padding:8px}.post__header .user{display:flex;align-items:center}.post__header .user__thumb{height:40px;margin-right:8px;width:40px}.post__header .user__name{color:black;font-size:1.2rem;font-weight:bold;text-decoration:none}.post__context{font-size:1.4rem}.post__figure{overflow:hidden;margin:0;height:auto;width:100%}.post__figure img{width:100%}.post__controls{width:100%;display:flex;justify-content:flex-start;align-items:center;padding:4px 0}.post__control{font-size:2.4rem;margin:0 8px;position:relative}.post__control:last-of-type{justify-self:flex-end;margin-left:auto}.post__status{display:flex;padding:8px;background-color:rgba(0,0,0,0.05)}.post__status .user{display:flex;justify-content:flex-start;align-items:center}.post__status .user span{font-size:1.2rem}.post__status .user__thumb{height:30px;width:30px;margin-right:8px}.comments__counter{align-items:center;background-color:red;border-radius:50px;color:white;display:flex;font-size:10px;font-weight:bold;height:20px;justify-content:center;overflow:hidden;position:absolute;right:-2px;top:-2px;width:20px}

.topbar{-webkit-backface-visibility:hidden;backface-visibility:hidden;background-color:white;left:0;padding:8px 16px;position:fixed;top:0;width:100%;box-shadow:0 1px 0 rgba(0,0,0,0.1);z-index:9999}.topbar .container{display:flex;justify-content:space-between}.topbar__logo{display:inline-block;height:40px}.topbar__logo svg{height:100%}.topbar__icon{font-size:1.6rem}.topbar__group{display:flex}.topbar__icon{align-items:center;display:flex;flex-direction:column;font-size:1.6rem;margin:auto 1rem;justify-content:center}.topbar__icon span{font-size:1.1rem}

*,*:after,*:before{box-sizing:border-box}html{font-size:10px}body{padding-top:56px;font-family:'Open Sans', sans-serif;background-color:#FAFAFA;color:#000}a{text-decoration:none;color:#000;font-weight:bold}button{background-color:transparent;border:none;box-shadow:none;outline:none}.container{width:100%;margin:0 auto}@media screen and (min-width: 728px){.container{width:728px}}.user__thumb{display:flex;border-radius:100%;overflow:hidden}.user__thumb__wrapper{border-radius:100%;border:2px solid white;display:inline-block;overflow:hidden}.user__thumb img{height:100%;width:100%}.user__thumb--hasNew{background:linear-gradient(45deg, #f5a04d 0%, #a00f8b 100%)}

